import React from 'react';
import Home from "./Home";
import NavBar from "./NavBar";
import Banks from "./Banks";
import Request from "./Request";
import Donate from "./Donate";
import BankDetails from "./BankDetails";

class App extends React.Component{
    //the state contains a component_id which selects which component to show
    state={component_id:0,selected_id:null,selected_bank:[]}

    changeComponentId = (id) =>{
        this.setState({component_id:id})
    }

    onRequest = (id) => {
        console.log(id)
        this.setState({component_id:4,selected_id:id})
    }
    onDonate = (id) => {
        this.setState({component_id:2,selected_id:id})
    }
    onDetails = (bank) =>{
        this.setState({component_id:3,selected_bank:bank})
    }
    render() {
        if (this.state.component_id===0){
            return(
                <div>
                    <NavBar changeId={this.changeComponentId}/>
                    <Home changeId={this.changeComponentId}/>
                </div>
            )
        }
        if (this.state.component_id===1){
            return(
                <div className="ui container">
                    <NavBar changeId={this.changeComponentId}/>
                    <Banks onRequest={this.onRequest} onDonate={this.onDonate} onDetails={this.onDetails}/>
                </div>
            )
        }
        if (this.state.component_id===4){
            //fot the request component
            return (
                <div>
                    <NavBar changeId={this.changeComponentId}/>
                    <Request bankId={this.state.selected_id} changeId={this.changeComponentId}/>
                </div>
            )
        }
        if (this.state.component_id===2){
            //fot the donate component
            return (
                <div>
                    <NavBar changeId={this.changeComponentId}/>
                    <Donate bankId={this.state.selected_id} changeId={this.changeComponentId}/>
                </div>
            )
        }
        if (this.state.component_id===3){
            //fot the donate component
            return (
                <div>
                    <NavBar changeId={this.changeComponentId}/>
                    <BankDetails bank={this.state.selected_bank[0]} changeId={this.changeComponentId}/>
                </div>
            )
        }

    }

}

export default App