//the component ued to display a single blood bank
import React from "react";
import "../styles/Banks.css"

const BloodBankCard = ({bank,onRequest,onDonate,onDetails})=> {
    const bankArray=[]//to pass the bank to the BankDetails component
    bankArray.push(bank)
    return(
        <div className="item bank-item">
            <div className="ui image">
                <img src="https://image.flaticon.com/icons/png/512/1297/1297136.png" alt="" />
            </div>
            <div className="content">
                <h4>{bank.hospital}</h4>
                <div className="meta">
                    <span>Address: {bank.address}</span>
                </div>
                <div className="description">
                    <p>Phone : {bank.phone}</p>
                    <p>O+: {bank.OP} O-: {bank.ON} A+:{bank.AP} A-:{bank.AN} B+: {bank.BP} B-: {bank.BN} AB+: {bank.ABP} AB-: {bank.ABN}</p>
                </div>
                <div className="extra">
                    <button className="ui red button" onClick={()=>onRequest(bank._id)}>Request</button>
                    <button className="ui green button" onClick={()=>onDonate(bank._id)}>Donate</button>
                    <button className="ui blue button" onClick={()=>onDetails(bankArray)}>Details</button>

                </div>
            </div>
        </div>
    )
}

export default BloodBankCard