import React from "react";
import axios from 'axios'

class Donate extends React.Component{
    state={name:'',group:'',quantity:null}

    onNameChange = (event) =>{
        this.setState({name:event.target.value})
    }

    onGroupChange = (event) =>{
        this.setState({group:event.target.value})
    }

    onQuantityChange = (event) =>{
        this.setState({quantity:event.target.value})
    }

    onFormSubmit = async (event) =>{
        console.log(event)
        event.preventDefault()
        axios.post('https://ancient-mountain-97159.herokuapp.com/donate', {
            id: this.props.bankId,
            name: this.state.name,
            group: this.state.group,
            quantity: this.state.quantity
        })
        this.props.changeId(1)
        alert("THANK YOU FOR YOUR DONATION")

    }
    render() {
        console.log(this.props.bankId)
        return(
            <div className="ui container form-container">
                <h1>Donate Blood</h1>
                <br/>
                <form action="/request" className="ui form" onSubmit={this.onFormSubmit} method="post">
                    <div className='field'>
                        <label htmlFor="">Name</label>
                        <input type="text" name="name" placeholder="name" onChange={this.onNameChange} required/>
                    </div>
                    <div className='field'>
                        <label htmlFor="">Blood Group</label>
                        <input onChange={this.onGroupChange} type="text" name="group" placeholder="blood group: OP/ON/AP/AN/BP/BN/ABP/ABN"required/>
                    </div>
                    <div className='field'>
                        <label htmlFor="">Quantity(ml)</label>
                        <input onChange={this.onQuantityChange} type="number" name="quantity" placeholder="enter the quantity in ml" required/>
                    </div>
                    <button className="ui button">Submit</button>
                </form>
            </div>

        )
    }
}
export default Donate