import React from "react";
import Navbar from 'react-bootstrap/Navbar'
import Nav from "react-bootstrap/Nav";
import "../styles/Home.css"

class NavBar extends React.Component{
    render() {
        const goToHome = () =>{
            this.props.changeId(0)
        }
        const findBank = () => {
            this.props.changeId(1)
        }
        const donate = () => {
            this.props.changeId(1)
        }
        return (
            <Navbar bg="dark" expand="lg" className="x" variant="dark" fixed="top">
                <Navbar.Brand href="#home" onClick={goToHome}>Donate</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                        <Nav.Link href="#home" onClick={goToHome}>Home</Nav.Link>
                        <Nav.Link href="#donate" onClick={donate}>Donate</Nav.Link>
                        <Nav.Link href="#request" onClick={findBank}>Request</Nav.Link>
                        <Nav.Link href="#vampire">Vampire</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        );
    }

}

export default NavBar