import React from "react";
import axios from 'axios'
import BloodBankCard from "./BloodBankCard";
import Spinner from "./Spinner";
import "../styles/Banks.css"


class Banks extends React.Component{
    state={banks:[],cards:[]}
    componentDidMount() {
        axios.get('https://ancient-mountain-97159.herokuapp.com/banks',{

        }).then(response => {
            // console.log(response)
            this.setState({banks:response.data})
            // console.log(this.state.banks)
        })

    }

    renderBanks = () =>{
        if (this.state.banks.length!==0){
            console.log("yes")
            const items=this.state.banks.map((bank)=>{
                return(
                    <div className="item">
                        <BloodBankCard bank={bank} onRequest={this.props.onRequest} onDonate={this.props.onDonate}
                            onDetails={this.props.onDetails}
                        />
                    </div>
                )
            })
            return(
                <div className="ui divided items bank-list">
                    {items}
                </div>
            )

        }
        return(
            <div><Spinner message="fetching data..."/></div>
        )

    }

    render() {
        return (
            <div>
                {this.renderBanks()}
            </div>
        )


}
}
export default Banks